@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Luckiest+Guy);
html, body{
  height: 100%;
  font-weight: 800;
}



svg {
    display: block;
    font: 10.5em 'Montserrat';
    width: 100%;
    height: 450px;
    margin: 0 auto;
}

.text-copy {
    fill: none;
    stroke: white;
    stroke-dasharray: 6% 29%;
    stroke-width: 5px;
    stroke-dashoffset: 0%;
    -webkit-animation: stroke-offset 5.5s infinite linear;
            animation: stroke-offset 5.5s infinite linear;
}

.text-copy:nth-child(1){
	stroke: rgb(34, 34, 34);
	-webkit-animation-delay: -1;
	        animation-delay: -1;
}

.text-copy:nth-child(2){
	stroke: rgb(55, 67, 77);
	-webkit-animation-delay: -2s;
	        animation-delay: -2s;
}

.text-copy:nth-child(3){
	stroke: rgb(235, 27, 114);
	-webkit-animation-delay: -3s;
	        animation-delay: -3s;
}

.text-copy:nth-child(4){
	stroke: rgba(40, 181, 225, 0.6);
	-webkit-animation-delay: -4s;
	        animation-delay: -4s;
}

.text-copy:nth-child(5){
	stroke: #FDB731;
	-webkit-animation-delay: -5s;
	        animation-delay: -5s;
}

@-webkit-keyframes stroke-offset{
	100% {stroke-dashoffset: -35%;}
}

@keyframes stroke-offset{
	100% {stroke-dashoffset: -35%;}
}
/* BODY */

/* JUMP */
.ani {
  cursor: default;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  margin: auto;
  display: block;
  text-align: center;
}

.ani span {
  position: relative;
  top: 20px;
  display: inline-block;
  -webkit-animation: bounce 0.3s ease infinite alternate;
  font-size: 80px;
  color: #fff;
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc,
    0 5px 0 #ccc, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent,
    0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, 0.4);
}

.ani span:nth-child(2) {
  -webkit-animation-delay: 0.1s;
}

.ani span:nth-child(3) {
  -webkit-animation-delay: 0.2s;
}

.ani span:nth-child(4) {
  -webkit-animation-delay: 0.3s;
}

.ani span:nth-child(5) {
  -webkit-animation-delay: 0.4s;
}

.ani span:nth-child(6) {
  -webkit-animation-delay: 0.5s;
}

.ani span:nth-child(7) {
  -webkit-animation-delay: 0.6s;
}

.ani span:nth-child(8) {
  -webkit-animation-delay: 0.2s;
}

.ani span:nth-child(9) {
  -webkit-animation-delay: 0.3s;
}

.ani span:nth-child(10) {
  -webkit-animation-delay: 0.4s;
}

.ani span:nth-child(11) {
  -webkit-animation-delay: 0.5s;
}

.ani span:nth-child(12) {
  -webkit-animation-delay: 0.6s;
}

.ani span:nth-child(13) {
  -webkit-animation-delay: 0.7s;
}

.ani span:nth-child(14) {
  -webkit-animation-delay: 0.8s;
}

/* ANIMATION */
@-webkit-keyframes bounce {
  100% {
    top: -20px;
    text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 3px 0 #ccc, 0 4px 0 #ccc,
      0 5px 0 #ccc, 0 6px 0 #ccc, 0 7px 0 #ccc, 0 8px 0 #ccc, 0 9px 0 #ccc,
      0 50px 25px rgba(0, 0, 0, 0.2);
  }
}
